@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.Project {
  box-sizing: border-box;
  padding: 1rem;
  background: #515151;
  border-bottom: 0.3rem solid orangered;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: all 0.4s ease; }
  .Project:hover {
    transform: scale(1.07); }
  .Project--heading {
    text-align: center;
    margin: 2rem auto; }
  .Project--image {
    display: block;
    width: 95%;
    margin: 0 auto; }
  .Project--description {
    text-align: center;
    margin: 1.5rem 0;
    flex-basis: 5rem; }
  .Project--buttons {
    display: flex;
    justify-content: space-around; }
    .Project--buttons a {
      text-decoration: none;
      color: #eee;
      font-size: 1.2rem;
      transition: all 0.4s ease; }
      .Project--buttons a:hover {
        color: orangered; }
      .Project--buttons a-selected {
        color: orangered; }

@media screen and (min-width: 0px) {
  .Project {
    width: 300px; } }

@media screen and (min-width: 500px) {
  .Project {
    width: 325px; } }

@media screen and (min-width: 1171px) {
  .Project {
    width: 325px; } }
