@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.AboutMe {
  margin-bottom: 5rem; }
  .AboutMe--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 1.5rem 0; }
  .AboutMe--subHeading {
    font-weight: 400;
    margin: 1.5rem 0; }
  .AboutMe--text-secondaryColor {
    color: orangered; }
  .AboutMe--main {
    max-width: 1100px;
    margin: 5rem auto;
    display: grid;
    grid-gap: 2.5rem;
    -webkit-align-items: center;
            align-items: center;
    justify-items: center;
    grid-template-areas: "portrait bio bio" "card card card"; }
  .AboutMe--image {
    grid-area: portrait;
    border-radius: 50%;
    border: solid 0.3rem orangered;
    width: 225px;
    height: 225px; }
  .AboutMe--bio {
    grid-area: bio;
    font-size: 1.2rem; }

.Card--front-end {
  grid-area: cfe; }

.Card--back-end {
  grid-area: cbe; }

.Card--other {
  grid-area: o; }

@media screen and (min-width: 0px) {
  .AboutMe--main {
    grid-template-columns: 1fr;
    -webkit-align-items: stretch;
            align-items: stretch;
    grid-template-areas: "portrait" "bio" "cfe" "cbe" "o"; } }

@media screen and (min-width: 768px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, minmax(10px, 1fr));
    grid-template-areas: "portrait bio bio" ". cfe ." ". cbe ." ". o ."; } }

@media screen and (min-width: 1171px) {
  .AboutMe--main {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "portrait bio bio" "cfe cbe o"; } }
