@import url("https://fonts.googleapis.com/css?family=Scope+One");
/* 
Media Queries written to be mobile first
Anything in small query carries to the largest unless over-ridden
*/
.MyWork {
  margin-bottom: 5rem; }
  .MyWork--heading {
    font-size: 6rem;
    font-weight: 400;
    margin: 1.5rem 0; }
  .MyWork--subHeading {
    font-weight: 400;
    margin: 1.5rem 0; }
  .MyWork--text-secondaryColor {
    color: orangered; }
  .MyWork--search {
    display: block;
    width: 75%;
    text-align: center;
    max-width: 600px;
    margin: 3rem auto;
    line-height: 1.5;
    padding: 1.3rem 1.5rem 1rem;
    background: #515151;
    color: #eee;
    border: none;
    outline: none;
    font-size: 1.5rem;
    border-bottom: 0.3rem solid #515151;
    transition: all 0.4s ease; }
    .MyWork--search:hover, .MyWork--search:focus {
      border-bottom: 0.3rem solid orangered; }
  .MyWork--suggestions {
    text-align: center; }
  .MyWork--tag {
    display: inline-block;
    background: orangered;
    color: #111;
    margin: 0.3rem;
    padding: 0.2rem 0.5rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: all 0.4s ease; }
    .MyWork--tag:hover {
      transform: scale(1.1); }
  .MyWork--main {
    max-width: 1100px;
    margin: 5rem auto;
    display: grid;
    grid-gap: 2.5rem;
    align-items: center;
    justify-items: center; }

@media screen and (min-width: 0px) {
  .MyWork--main {
    grid-template-columns: 1fr;
    align-items: stretch; } }

@media screen and (min-width: 768px) {
  .MyWork--main {
    grid-template-columns: repeat(2, 1fr); } }

@media screen and (min-width: 1171px) {
  .MyWork--main {
    grid-template-columns: repeat(3, 1fr); } }
